<template>
  <div>
    <h1></h1>
    <form @submit.prevent="POST_create">
        <select v-model="tag_pk" >
          <option :value="1"></option>
          <option :value="2"></option>
          <option :value="3"></option>
          <option :value="4"></option>
        </select>
        <div>
          <label for="new-article-title">: </label>
          <input type="text" id="new-article-title" placeholder="" v-model="title">
        </div>
        <div>
          <label for="new-article-conent">: </label>
          <input type="text" id="new-article-conent" placeholder="" v-model="content">
        </div>
        <input type="submit">
    </form>
  </div>
</template>

<script>
export default {
  name: "CreateArticleView",
  data(){
    return {
      title: '',
      content: '',
      tag_pk: 1,
      image_profile:''
    }
  },
  methods: {
    
    async POST_create(){
      // {title, content, tag_pk, by, key}
      // await this.$store.dispatch("POST_create", {title: " ", content: "", tag_pk: 1, by:"id", key:502356})
      const {title, content, tag_pk} = this
      const {by, key} = this.$route.params
      const {id} = await this.$store.dispatch("POST_create", {title, content, tag_pk, by, key})
      this.$router.push({ name: 'articleDetail', params: {articlePk:id} })
    }
  }

}
</script>

